import Vue from "vue";
import App from "./App.vue";
import store from "./store";
import router from "./router";
import VueNativeSock from "vue-native-websocket";
import "materialize-css/dist/js/materialize.min";

Vue.use(
  VueNativeSock,
  `wss://${process.env.VUE_APP_host || location.host}/?uid=${localStorage.uid}`,
  {
  store,
  reconnection: true
});

new Vue({
  store,
  router,
  render: h => h(App)
}).$mount("#app");
