<template>
  <div class="main-layout">
    <Sidebar />
    <transition :name="transitionName">
      <router-view class="fullscreen content" />
    </transition>
  </div>
</template>

<script>
import Sidebar from "@/components/Sidebar.vue";

export default {
  props: {
    meta: Object
  },
  data: () => ({
    index: 0,
    transitionName: "slide-left"
  }),
  beforeCreate() {
    fetch(
      `//${process.env.VUE_APP_host || location.host}/exist/${localStorage.uid}`
    )
      .then(res => res.json())
      .then(exist => {
        if (!exist) this.$router.replace("login");
      })
      .catch(() => this.$router.replace("login"));
  },
  mounted() {
    this.index = this.meta.index || 0;
  },
  watch: {
    $route(to) {
      const new_index = to.meta.index;
      this.transitionName = new_index < this.index ? "back" : "next";

      this.index = new_index;
    }
  },
  components: {
    Sidebar
  }
};
</script>

<style scoped>
@import "../assets/mainlayout.css";
</style>
